<template>
    <div>
		<el-table :data="tableData">
			<el-table-column prop="student.number" label="学号" align="center"></el-table-column>
			<el-table-column prop="student.name" label="学生姓名" align="center"></el-table-column>
			<el-table-column prop="UserInfo.name" label="家长姓名" align="center"></el-table-column>
			<el-table-column prop="UserInfo.phone" label="家长手机号" align="center"></el-table-column>
			<el-table-column prop="WxUser.nickname" label="家长微信名" align="center"></el-table-column>
			<el-table-column prop="UserInfo.href" label="家长人脸图像" align="center">
				<template slot-scope="scope"><img :src="scope.row.UserInfo.href" style="max-width:180px;max-height:80px;" /></template>
			</el-table-column>
			<!-- <el-table-column prop="remark" label="状态" align="center"></el-table-column> -->
			<el-table-column prop="UserInfo.updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleMore(scope.$index, scope.row)">查看更多</el-button>
				</template>
			</el-table-column>
		</el-table>
		
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
		
		<el-dialog title="学生家庭成员列表" :visible.sync="dialogFamily" width='90%' align="center">
			<table border="1" width="1000px" class="table" :model="studentList">
				<tr class="trHeight">
					<td rowspan='2'>学生信息</td>
					<td class="bgcolor">学号</td>
					<td class="bgcolor">学生姓名</td>
					<td class="bgcolor">性别</td>
					<td class="bgcolor">年龄</td>
					<td class="bgcolor">班级</td>
				</tr>
				<tr>
					<td>{{studentList.number}}</td>
					<td>{{studentList.name}}</td>
					<td>{{studentList.sex === 1 ? '男' : '女'}}</td>
					<td>{{studentList.age}}</td>
					<td>{{studentList.class_id}}</td>
				</tr>
			</table>
			<table border="1" width="1000px" class="table" :model="tableList">
				<tr class="trHeight">
					<td rowspan='2'>默认家长</td>
					<td class="bgcolor">家长ID</td>
					<td class="bgcolor">家长名称</td>
					<td class="bgcolor">性别</td>
					<td class="bgcolor">电话</td>
					<td class="bgcolor">身份证</td>
					<td class="bgcolor">地址</td>
				</tr>
				<tr>
					<td>{{tableList.id}}</td>
					<td>{{tableList.name}}</td>
					<td>{{tableList.sex === 1 ? '男' : '女'}}</td>
					<td>{{tableList.phone}}</td>
					<td>{{tableList.id_card}}</td>
					<td style="text-align: start;">{{tableList.address}}</td>
				</tr>
			</table>
<!-- 			<div v-for="(item,index) in tableList" :key="index">
				<table border="1" width="1300px" class="table">
					<tr class="trHeight">
						<td rowspan='2'>家长信息</td>
						<td class="bgcolor">家长ID</td>
						<td class="bgcolor">家长名称</td>
						<td class="bgcolor">性别</td>
						<td class="bgcolor">电话</td>
						<td class="bgcolor">身份证</td>
						<td class="bgcolor">地址</td>
					</tr>
					<tr>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
					</tr>
				</table>
			</div> -->
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/index.js'
	export default {
		name: 'family',
		data() {
			return {
				tableData: [{}],
				dialogFamily: false,
				studentList: {},
				tableList: [{},{}],
				familyList: [],
				currentPage: 1,
				totalPage: 0,
				
				permissions: localStorage.getItem('permissions'),
				role: localStorage.getItem('role')
			}
		},
		mounted() {
			this.getFamily();
		},
		methods: {
			// 获取家长
			getFamily() {
				var self = this;
				if(self.permissions.indexOf('familyGet') > -1 || self.role === 'super') {
					API.family(self.currentPage).then(res => {
						self.tableData = res.data;
						self.totalPage = res.total;
					})
				} else {
					self.$message.warning('无此权限');
				}
			},
			handleMore(index, row) {
				console.log(row)
				var self = this;
				if(self.permissions.indexOf('familyMore') > -1 || self.role === 'super') {
					self.dialogFamily = true;
					self.studentList = row.student;
					self.tableList = row.UserInfo;
				} else {
					self.$message.warning('无此权限');
				}
			},
			
			// 返回得数据有多个家长信息，推入一个表格
			// getMoreInfo() {
			// 	this.familyList.push({})
			// },
			
			
			// 分页
			handleCurrentChange(val) {
				var self = this;
				this.getFamily()
			},
		}
	}
</script>

<style lang="scss" scoped>
	.table {
		height: 100px;
		text-align: center;
		box-shadow: 0 2px 4px 0;
		margin-top: 20px;
	}

	.table .bgcolor {
		background-color: #EBEEF5;
	}

	.table .trHeight {
		height: 40px;
	}

	.table td {
		width: 40px;
	}
</style>

